@media print {
  /* your print styles */
  .raleigh-campaign.Campaign.CampaignType--floating div,
  .site-header .header-right-column,
  #page .site-main .post-nav-wrapper,
  #page .site-main .social-share-wrapper,
  aside.sidebar .location-callout-box-basic,
  aside.sidebar .featured-posts-block,
  aside.sidebar .categories-block,
  #page .site-main .call-to-action,
  footer.primary-footer {
    display: none;
  }
}

#page .site-main {
  padding: 28px 0 85px 0;
}

@media (max-width: 767.98px) {
  #page .site-main {
    padding: 25px 0 55px 0;
  }
}

#page .site-main h1 {
  margin-bottom: 2.5rem;
}

@media (max-width: 767.98px) {
  #page .site-main h1 {
    margin-bottom: 20px;
  }
}

#page .site-main h2 {
  font-size: 1.5rem;
  font-weight: 500;
  letter-spacing: -0.025rem;
  margin: 1.5rem 0 1.5rem;
  line-height: 1.2;
  color: #084C61;
}

#page .site-main h2 span {
  font-weight: 500 !important;
}

#page .site-main h3 {
  color: #084C61;
}

#page .site-main .banner-img-wrapper .post-thumbnail img {
  min-height: auto;
}

#page .site-main .post-excerpt-wrapper {
  padding: 0 0 2rem;
  margin: 2.8125rem 0;
  border-bottom: 1px solid #f4f4f4;
}

#page .site-main .post-content-wrapper {
  font-size: 16px;
  line-height: 1.5rem;
  font-weight: 300;
  margin-top: 2.5rem;
  margin-bottom: 3rem;
}

@media (max-width: 767.98px) {
  #page .site-main .post-content-wrapper {
    margin-bottom: 20px;
  }
}

#page .site-main .post-content-wrapper h1:first-child {
  display: none;
}

#page .site-main .post-content-wrapper a {
  font-weight: 600 !important;
}

#page .site-main .post-content-wrapper a span {
  font-weight: 600 !important;
}

#page .site-main .post-content-wrapper ul {
  margin-block-start: 0;
  margin-left: 0;
  /* for IE */
  padding-inline-start: 20px;
  padding-left: 20px;
  /* for IE */
}

#page .site-main .post-content-wrapper ul li a {
  text-decoration: underline;
}

#page .site-main .post-nav-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

#page .site-main .post-nav-wrapper a {
  margin-right: 1rem;
  color: #1a1a1a;
  text-decoration: underline;
}

@media (max-width: 767.98px) {
  #page .site-main .post-nav-wrapper a:last-child {
    float: right;
    margin-right: 0;
  }
  #page .site-main .post-nav-wrapper a {
    font-size: 16px;
  }
}

#page .site-main .social-share-wrapper {
  text-align: center;
}

#page .site-main .social-share-wrapper h2 {
  font-size: 18px;
  line-height: 20px;
  font-family: 'Gotham';
  margin: 0 0 15px;
  color: #1a1a1a;
}

#page .site-main .social-share-wrapper .social-box {
  margin: 0;
}

@media (max-width: 767.98px) {
  #page .site-main .social-share-wrapper {
    margin: 30px 0;
  }
}

#page .site-main .call-to-action {
  max-width: 1200px;
}

